@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;1,300;1,400&display=swap);
body {
  min-height: 100vh;
  margin: 0px;
  font-family: 'Roboto', sans-serif;
}

/* width */
::-webkit-scrollbar {
  width: 0.5em;
  height: 0.5em;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 4px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #0895fe;
  border-radius: 4px;
}

.footer{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}


@keyframes flip-right {
  0%{transform: translate(60.0%, 0)}

  100%{transform: translate(0.0%, 0)}
}

@keyframes flip-left {
  0%{transform: translate(-60.0%, 0)}

  100%{transform: translate(0.0%, 0)}
}




.flip-right{
  animation: flip-right 0.5s linear 1;
  will-change: animation;
  position: relative;
}
.flip-left{
  animation: flip-left 0.5s linear 1;
  will-change: animation;
  position: relative;
}

